"use client"

import React from "react"
import { Motor } from "@/types/motor"
import MotorForm from "./MotorForm"

interface MotorModalProps {
  motor: Motor
  onSave: (data: Omit<Motor, 'id'>) => void
  onClose: () => void
  isOpen: boolean
}

export default function MotorModal({ motor, onSave, onClose, isOpen }: MotorModalProps) {
  if (!isOpen) return null

  return (
    <div className="fixed inset-0 z-50">
      {/* Backdrop */}
      <div className="absolute inset-0 bg-black/60 backdrop-blur-sm" onClick={onClose} />
      
      {/* Modal */}
      <div className="absolute inset-4 md:inset-8 bg-gradient-to-br from-gray-900 via-black to-blue-950 rounded-xl shadow-2xl flex flex-col">
        {/* Decorative elements */}
        <div className="absolute inset-0 bg-[url('/grid.svg')] bg-center [mask-image:linear-gradient(180deg,white,rgba(255,255,255,0))] opacity-25 pointer-events-none" />
        <div className="absolute inset-px bg-black/50 rounded-xl pointer-events-none" />
        
        {/* Content */}
        <div className="relative flex-1 overflow-hidden rounded-xl">
          {/* Header */}
          <div className="flex items-center justify-between px-6 py-4 border-b border-gray-700/50">
            <h2 className="text-2xl font-bold bg-gradient-to-r from-orange-400 to-white bg-clip-text text-transparent">
              Editar Motor: {motor.name}
            </h2>
            <button
              onClick={onClose}
              className="p-2 text-gray-400 hover:text-white transition-colors rounded-lg hover:bg-white/5"
              aria-label="Cerrar"
            >
              <svg
                className="h-6 w-6"
                fill="none"
                strokeLinecap="round"
                strokeLinejoin="round"
                strokeWidth="1.5"
                viewBox="0 0 24 24"
                stroke="currentColor"
              >
                <path d="M6 18L18 6M6 6l12 12"></path>
              </svg>
            </button>
          </div>

          {/* Form */}
          <div className="p-6 overflow-y-auto max-h-[calc(100vh-12rem)]">
            <MotorForm
              initialData={motor}
              onSubmit={(data) => {
                onSave(data)
                onClose()
              }}
              onCancel={onClose}
              submitLabel="Guardar Cambios"
            />
          </div>
        </div>
      </div>
    </div>
  )
}
