"use client"

import { useState } from "react"
import { Motor } from "@/types/motor"
import { useLocalStorage } from "@/hooks/useLocalStorage"
import MotorForm from "@/components/MotorForm"
import MotorTable from "@/components/MotorTable"
import MotorModal from "@/components/MotorModal"

export default function Home() {
  const [motors, setMotors] = useLocalStorage<Motor[]>("motors", [])
  const [editingMotor, setEditingMotor] = useState<Motor | null>(null)

  const handleAddMotor = (motorData: Omit<Motor, "id">) => {
    try {
      const newMotor: Motor = {
        ...motorData,
        id: Date.now(),
      }
      setMotors((prev) => [newMotor, ...prev])
      showNotification("Motor agregado exitosamente")
    } catch (error) {
      console.error("Error al agregar motor:", error)
      showNotification("Error al agregar motor", true)
    }
  }

  const handleEditMotor = (motorData: Omit<Motor, "id">) => {
    try {
      if (!editingMotor) return
      const updatedMotors = motors.map((motor) =>
        motor.id === editingMotor.id ? { ...motorData, id: motor.id } : motor
      )
      setMotors(updatedMotors)
      setEditingMotor(null)
      showNotification("Motor actualizado exitosamente")
    } catch (error) {
      console.error("Error al actualizar motor:", error)
      showNotification("Error al actualizar motor", true)
    }
  }

  const handleDeleteMotor = (id: number) => {
    try {
      setMotors((prev) => prev.filter((motor) => motor.id !== id))
      showNotification("Motor eliminado exitosamente")
    } catch (error) {
      console.error("Error al eliminar motor:", error)
      showNotification("Error al eliminar motor", true)
    }
  }

  const showNotification = (message: string, isError = false) => {
    // In a real app, you would use a toast notification library
    alert(message)
  }

  return (
    <>
      <main className="min-h-screen bg-[radial-gradient(ellipse_at_top,_var(--tw-gradient-stops))] from-gray-900 via-black to-blue-950 text-white pt-24 pb-32">
        <div className="max-w-7xl mx-auto px-6 space-y-12">
          {/* Decorative elements */}
          <div className="absolute top-0 left-0 right-0 h-96 bg-gradient-to-b from-orange-500/5 to-transparent pointer-events-none" />
          <div className="absolute inset-0 bg-[url('/grid.svg')] bg-center [mask-image:linear-gradient(180deg,white,rgba(255,255,255,0))] pointer-events-none" />
          
          <section id="add-motor" className="relative">
            <div className="absolute -inset-1 bg-gradient-to-r from-orange-600 to-orange-400 rounded-2xl blur opacity-25" />
            <div className="relative p-8 bg-gray-900/90 backdrop-blur-xl rounded-xl border border-orange-500/20 shadow-2xl">
              <h2 className="text-2xl font-bold bg-gradient-to-r from-orange-400 to-white bg-clip-text text-transparent mb-6">
                Agregar Motor
              </h2>
              <MotorForm onSubmit={handleAddMotor} />
            </div>
          </section>

          <section id="motors" className="relative">
            <div className="absolute -inset-1 bg-gradient-to-r from-blue-600 to-blue-400 rounded-2xl blur opacity-25" />
            <div className="relative p-8 bg-gray-900/90 backdrop-blur-xl rounded-xl border border-blue-500/20 shadow-2xl">
              <h2 className="text-2xl font-bold bg-gradient-to-r from-blue-400 to-white bg-clip-text text-transparent mb-6">
                Motores Registrados
              </h2>
              <MotorTable
                motors={motors}
                onEdit={setEditingMotor}
                onDelete={handleDeleteMotor}
              />
            </div>
          </section>
        </div>

        <MotorModal
          isOpen={!!editingMotor}
          motor={editingMotor!}
          onSave={handleEditMotor}
          onClose={() => setEditingMotor(null)}
        />
      </main>
    </>
  )
}
